.tag {
  list-style: none;
  overflow: hidden;
  background: #eee;
  border-radius: 3px 0 0 3px;
  color: #999;
  display: inline-block;
  height: 26px;
  line-height: 26px;
  padding: 0 20px 0 23px;
  position: absolute;
  left: 8px;
  top: -2px;
  text-decoration: none;
  &:before {
    background: #fff;
    border-radius: 10px;
    box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
    content: "";
    height: 6px;
    left: 10px;
    position: absolute;
    width: 6px;
    top: 10px;
  }
  &:after {
    background: #fff;
    border-bottom: 13px solid $darkGreen;
    border-left: 10px solid #eee;
    border-top: 13px solid transparent;
    content: "";
    position: absolute;
    right: 0;
    top: 0;
  }
}
.scheduleList {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
  margin: auto;
  margin-top: 10px;
  border: 1px solid $gray;
  border-radius: 10px;
  overflow: hidden;
  width: 220px;
  height: 200px;
  margin: 10px 20px 15px 20px;
  cursor: pointer;
  .scheduleTitle {
    text-align: center;
    font-size: 20px;
    background-color: $darkGreen;
    color: white;
    border-radius: 9px 9px 0 0;
    padding: 8px;
    width: 100%;
    margin: 0;
  }
  .date {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
    font-size: 13px;
    color: $darkGray;
    svg {
      color: $darkGreen;
      padding: 5px 8px 5px 8px;
    }
  }
}

.userList {
  color: $darkGray;
  border-right: 1px solid $lineGray;
  height: 440px;
  width: 172px;
  margin-top: 70px;

  /* Border X get width  */

  .border {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin-top: -20px;
    a {
      margin: 10px;
      font-size: 20px;
      letter-spacing: 5px;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      cursor: pointer;
      position: relative;
      padding: 10px 0 10px 0;

      &:before,
      &:after {
        position: absolute;
        opacity: 0;
        width: 0%;
        height: 1px;
        content: "";
        background: $darkGreen;
        transition: all 0.3s;
        z-index: 100;
      }
      &:before {
        left: 0px;
        top: 0px;
      }
      &:after {
        right: 0px;
        bottom: 0px;
      }

      &:hover {
        &:after {
          opacity: 1;
          width: 100%;
        }
      }

      &:hover {
        &:before {
          opacity: 1;
          width: 100%;
        }
      }
    }
  }
}
