.switchBtn {
  position: fixed;
  top: 75px;
  left: 350px;
  z-index: 150;
  opacity: 0;
  transition: opacity 1s;

  .btn {
    width: 150px;
    height: 70px;
    display: inline-block;
    border: 1px solid #fff;
    position: relative;
    overflow: hidden;
    span {
      color: $darkGray;
      font-size: 20px;
      line-height: 70px;
    }
    &:hover {
      background-color: $darkGreen;
      span {
        color: white;
      }
      .t1 {
        border-bottom: 120px solid #fff;
        transition: all 0.5s;
      }
      .t2 {
        border-top: 80px solid #fff;
        transition: all 0.5s;
      }
    }
  }

  .triangle {
    width: 0;
    height: 0;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
  }
  .t1 {
    border-bottom: 0px solid #fff;
    border-left: 70px solid transparent;
    border-right: 70px solid transparent;
    bottom: 0;
    opacity: 0.3;
    transition: all 0.5s;
  }
  .t2 {
    border-top: 0px solid #fff;
    border-left: 70px solid transparent;
    border-right: 70px solid transparent;
    top: 0;
    opacity: 0.3;
    transition: all 0.5s;
  }
}
