.scheduleListTopLeft {
  position: fixed;
  top: 75px;
  width: 350px;
  z-index: 200;

  .arrowBounce {
    cursor: pointer;
    text-align: center;
    margin: 8% 0;
    animation: bounce 2s 2;
    transform: rotate(0);
  }

  @keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateX(0);
    }
    40% {
      transform: translateX(-30px);
    }
    60% {
      transform: translateX(-15px);
    }
  }

  div {
    position: relative;
    .arrowBounce {
      font-size: 35px;
      position: absolute;
      right: 20px;
      top: -10px;
      svg {
        color: white;
        filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
      }
    }
    .scheduleLayer {
      position: absolute;
      background-color: $darkGray;
      opacity: 0.5;
      width: 350px;
      height: 80px;
      bottom: 6px;
    }
    img {
      width: 100%;
      height: 290px;
      object-fit: cover;
    }
  }
  .scheduleTitle {
    z-index: 160;
    position: absolute;
    bottom: 45px;
    left: 30px;
    font-size: 20px;
    font-weight: 400;
    color: white;
  }
  .date {
    z-index: 160;
